import { Menu } from "antd";
import { useNavigate } from "react-router-dom";

export default function Menus() {
  const navigate = useNavigate()

  const menuItems = [
    {
      label: '',
      key: '/route',
      children: [
        {
          label: '',
          key: '/route/basic',
        },
        {
          label: '',
          key: '/route/params',
        }
      ],
    },
    {
      label: '',
      key: '/redux',
      children: [
        {
          label: '',
          key: '/redux/basic',
        }
      ],
    }
  ]
  const changeMenu = ({ key }) => {
    navigate(key)
  }
  return (
    <Menu items={menuItems} mode="inline" onClick={changeMenu} />
  )
}
